.wrapper {
    width: 50%;
    top: 20%;
    left: 57%;
    position: absolute;
    opacity: 0;
    transition: 250ms opacity ease-in-out;
    transition-delay: 250ms;
}

.wrapper[data-active]{
    opacity: 1;
    z-index: 1;
    transition-delay: 0ms;
}

.wrapper img {
    border: 1px solid black;
    border-radius: 4%;
    width: 75%;
    box-shadow: -5px 5px 5px black;
}

.forward-button {
    background: none;
    font-size: 300%;
    border: none;
    position: absolute;
    z-index: 2;
    left: 90%;
    color:  rgb(224, 222, 222);
    cursor: pointer;
    background-color: rgba(0, 0, 0, .1);
    top: 42%;
}

.backwards-button {
    background: none;
    font-size: 300%;
    border: none;
    position: absolute;
    z-index: 2;
    top: 42%;
    left: 58%;
    color:  rgb(224, 222, 222);
    cursor: pointer;
    background-color: rgba(0, 0, 0, .1);
}

.forward-button:hover, .backwards-button:hover {
    color: white;
    background-color: rgba(0, 0, 0, .2);
    border: 1px solid black;
}

.github-links {
    position: absolute;
    top: 1%;
    right: 1%;
    font-size: 2.5vw;
    color: black;
}

.linkedin-links {
    position: absolute;
    top: 1%;
    right: 5%;
    font-size: 2.5vw;
    color: rgb(41, 106, 226);
}

.map-stuff {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 5%;
}

.switch {
    position: relative;
    display: inline-block;
    margin-top: 4%;
    width: 60px;
    height:34px;

  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #2196F3;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }

  .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

  .toggle-switch-text {
    font-family: 'Indie Flower', cursive;
    font-size: 2vw;
  }

  .chart-title {
    font-family: 'Indie Flower', cursive;
    font-size: 2vw;
  }